.coaster-card {
    background: #ffffff;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    transition: box-shadow 0.3s ease;
    text-align: left;
  }
  
  .coaster-card:hover {
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .coaster-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .coaster-details {
    padding: 10px;
  }
  
  .coaster-details h3 {
    font-size: 1.5rem;
    margin: 0 0 10px 0;
  }
  
  .coaster-details a {
    color: #007bff;
    text-decoration: none;
  }
  
  .coaster-details a:hover {
    text-decoration: underline;
  }