.coaster-page {
    font-family: 'Arial', sans-serif;
    padding: 20px;
    text-align: center;
    background-color: #f8f9fa;
    color: #333;
    margin-top: 60px;
  }
  
  .coaster-header {
    margin-bottom: 20px;
  }
  
  .coaster-header h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }
  
  .coaster-stats {
    display: flex;
    justify-content: center;
    gap: 20px;
    font-size: 1.2rem;
    margin-bottom: 20px;
  }
  
  .coaster-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
  }
  

  .map-section {
    margin: 20px 0;
  }
  
  .park-info {
    margin-top: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    background: #f9f9f9;
    border-radius: 8px;
  }
  
  .park-info h3 {
    margin: 0;
    color: #42a5f5;
  }

  .leaflet-container {
    border: 2px solid #ccc;
    border-radius: 8px;
  }